:root {
  --background-light: #f0f2f5;
  --content-bg-light: #ffffff;
  --text-color-light: #333;
  --sidebar-bg-light: #e9ecef;
  --sidebar-text-light: #212529;
  --sidebar-active-text-light: #0d6efd;
  --accent-light: #0d6efd;

  --background-dark: #121212;
  --content-bg-dark: #1e1e1e;
  --text-color-dark: #e0e0e0;
  --sidebar-bg-dark: #282c34;
  --sidebar-text-dark: #a0a0a0;
  --sidebar-active-text-dark: #61dafb;
  --accent-dark: #61dafb;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-light);
  color: var(--text-color-light);
  transition: background-color 0.3s, color 0.3s;
}

.App {
  display: flex;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

.sidebar {
  width: 250px;
  background-color: var(--sidebar-bg-light);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 1000;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out, background-color 0.3s;
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar nav {
  flex-grow: 1;
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar nav .nav-link {
  color: var(--sidebar-text-light);
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  transition: color 0.2s;
  position: relative;
}

.sidebar nav .nav-link:hover,
.sidebar nav .nav-link.active {
  color: var(--sidebar-active-text-light);
}

.content {
  flex-grow: 1;
  padding: 2rem;
  padding-left: 5rem; /* This ensures content is not hidden behind the menu button */
  transition: margin-left 0.3s ease-in-out;
}

.menu-button {
  background-color: var(--sidebar-active-text-light);
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  position: fixed;
  top: 1rem;
  left: 1rem;
  z-index: 1001;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.overlay.show {
  opacity: 1;
  visibility: visible;
}

.content-section {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.content-section h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.content-section p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.responsive-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-top: 1.5rem;
}

.App * {
  font-size: 18px !important;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--background-dark);
    color: var(--text-color-dark);
  }
  .sidebar {
    background-color: var(--sidebar-bg-dark);
  }
  .sidebar nav .nav-link {
    color: var(--sidebar-text-dark);
  }
  .sidebar nav .nav-link:hover,
  .sidebar nav .nav-link.active {
    color: var(--sidebar-active-text-dark);
  }
  .menu-button {
    background-color: var(--sidebar-active-text-dark);
    color: var(--sidebar-bg-dark);
  }
}
